/*/ Theme's for background, add .component to end for indiv use /*/
#light {
    background-color: #b6ccd1;
}
#dark {
    background-color: #0c3345;
}

body {
    margin: 0;
}

.nav {
    display: flex;
    justify-content: space-between;
    color: #5c1673;
    align-items: center;
    padding: 1rem;
}
.nav a {
    color: inherit;
    text-decoration: none;
}
.nav ul {
    list-style: none;
}
.site-title {
    font-size: 2rem;
    font-family:sans-serif;
    font-weight: bolder;
}

#light .nav {
    background-color: #819094;
}

#dark .nav{
    background-color: #08222e;
}